<template>
  <div id="app" class="container">
    <div class="tile is-ancestor">
      <div class="tile is-vertical is-12 is-parent">
        <div class="tile is-child box" v-if="deferredPrompt">
          <button class="is-fullwidth" @click="install">Instalar app</button>
        </div>
        <div class="tile is-child box">
          <h1>Introduzca el número telefónico sin el código de país.</h1>
          <p>
            puede contener cualquier símbolo. Se conservarán solo los números.
            Válido solo para Costa Rica (506)
          </p>
          <div class="field">
            <label class="label" for="fieldPhone">Número de teléfono</label>
            <div class="control">
              <input
                class="input"
                type="text"
                v-model="phone"
                id="fieldPhone"
              />
            </div>
          </div>
        </div>
        <div class="tile is-child box">
          <a class="button is-fullwidth" :href="'https://api.whatsapp.com/send?phone=506' + finalPhone" role="button">{{ finalPhone }}</a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import 'bulma/css/bulma.min.css'
export default {
  name: "App",
  data() {
    return {
      deferredPrompt: null,
      phone: "",
    };
  },
  created() {
    window.addEventListener("beforeinstallprompt", (e) => {
      e.preventDefault();
      // Stash the event so it can be triggered later.
      this.deferredPrompt = e;
    });
    window.addEventListener("appinstalled", () => {
      this.deferredPrompt = null;
    });
  },
  methods: {
    async install() {
      this.deferredPrompt.prompt();
    },
  },
  computed: {
    finalPhone() {
      return this.phone.replace(/[^\d]/g, "");
    }
  }
};
</script>

<style>

#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>
